import React, { Component } from 'react';
import Slider from "react-slick";
import ReactPlayer from 'react-player';
// import "slick-carousel/slick/slick.css";
// import "slick-carousel/slick/slick-theme.css";

class Videos extends Component {
  render() {
    const settings = {
      dots: true,
      className: "center",
      centerMode: true,
      // lazyLoad: true,
      centerPadding: "6%",
      infinite: false,
      slidesToShow: 1,
      speed: 500
    };

    if(this.props.data){
      var items = this.props.data.videos.map(function(video){
        return (
          <div key={video.url}>
            <ReactPlayer url={video.url} controls width="90%" height="90%"/>
          </div>
        )
      })
    }

    return (
      <section id="videos">
        <div className="row">
          <div>
            <h1>Algunos vídeos</h1>
            <Slider {...settings}>
              {items}
            </Slider>
          </div>
        </div>
      </section>
    );
  }
}

export default Videos;
